<template>
  <div class="siteLoading" v-show="show">
    <div class="loadingLines" :class="{ show: show }">
      <div class="line1"></div>
      <div class="line2"></div>
    </div>
  </div>
</template>
<script>
import CashDom from 'cash-dom'

export default {
  props: ['show'],
  watch: {
    show (isShow) {
      this.toggleBodyClass(isShow)
    }
  },
  mounted () {
    this.toggleBodyClass(this.show)
  },
  methods: {
    toggleBodyClass (isShow) {
      if (isShow) {
        CashDom(window.document.body).addClass('siteLoading-open')
      } else {
        CashDom(window.document.body).removeClass('siteLoading-open')
      }
    }
  }
}
</script>
<style lang="scss">

.siteLoading-open {
    overflow: hidden;
}

.siteLoading {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #1f1f23;
  z-index: 1;
}

.siteLoading.hide {
  opacity: 0;
  filter: alpha(opacity=0);
  visibility: hidden;
  transition: all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);
  /* easeOutQuart */
}

.siteLoading .loadingLines .line1,
.siteLoading .loadingLines .line2 {
  background: #ffffff;
}

</style>



