import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import { createListView } from '../views/CreateListView'
import AboutView from '../views/About.vue'

export default new Router({
  mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    { path: '/', component: createListView('design') },
    { path: '/design', component: createListView('design') },
    { path: '/photography', component: createListView('photography') },
    { path: '/profile', component: AboutView },
    { path: '/design*', redirect: '/design' },
    { path: '/photography*', redirect: '/photography' },
    { path: '/profile*', redirect: '/profile' },
    { path: '*', redirect: '/' }
  ]
})



