<template>
  <div class="work-item" :class="{ rev: !itemOdd, open: toggleOpen, ov: workItemOv}" ref="workItem">
    <div class="info info-black" :class="{ fixed: infoFixed }" ref="infoBlack" :style="{ top: infoTop + 'px'}">
        <div class="infoClip">
            <div class="infoInr">
              <h3 :style="styleInfoHeader">
                <div class="inr" ref="infoHeader"><a href="javascript:void(0);" :style="{ cursor: triggerCursor }" @click="triggerClick" @mouseover="triggerOver" @mouseout="triggerOut">{{ itemTitle }}<br>{{ item.location }}</a></div>
              </h3>
              <div class="date">posted at {{ item.year }}.{{ item.month }}.{{ item.dayInMonth }}</div>
              <div class="desc" :style="styleInfoDesc">
                <div class="inr" ref="infoDesc">{{ item.desc }}<template v-for="tool in item.tools"><br/>{{ tool }}</template></div>
              </div>
            </div>
        </div>
        <div class="mark mark-plus">+</div>
        <div class="mark mark-minus">_</div>
    </div>
    <!-- /info -->
    <div class="picArea" ref="picArea">
        <div class="listWrap" :style="{ height: listWrapHeight + 'px'}">
            <div class="list" :class="{ show: listShowClass }" ref="picAreaList">
                <div class="row" v-for="itemPic in item.content" ref="pictures">
                    <div class="pic" data-clr="white" style="width: 800px; height: 600px;"><img alt="item.desc" width="800" height="600" :src="itemPic.src" class="show"></div>
                </div>
            </div>
        </div>
        <!-- /listWrap -->
        <div class="teaser" :class="{ abs: teaserAbsClass, hide: teaserHideClass }">
            <a href="javascript:void(0);" :style="{ cursor: triggerCursor }" @click="triggerClick" @mouseover="triggerOver" @mouseout="triggerOut">
                <div class="pic" :class="{ hide: !teaserPicShow }" style="width: 800px; height: 600px;" ref="originPicture"><img :alt="item.desc" width="800" height="600" :src="item.cover" class="show"></div>
            </a>
            <div class="black" :class="{ show: teaserLoadingBgShow }"></div>
            <div class="loadingLines" :class="{ show: teaserLoadingShow }">
                <div class="line1"></div>
                <div class="line2"></div>
            </div>
            <div class="scrollArw" :class="{ show: teaserScrollArrowShow }">
                <div class="arw1"></div>
                <div class="arw2"></div>
            </div>
        </div>
        <!-- /teaser -->
    </div>
    <!-- /picArea -->
    <div class="info info-white" :class="{ fixed: infoFixed }" ref="infoWhite" :style="{ top: infoTop + 'px'}">
        <div class="infoClip" :style="styleInfoWhite">
            <div class="infoInr" :style="styleInfoWhiteRev">
              <h3 :style="styleInfoHeader">
                <div class="inr" :class="{black: infoWhiteHeaderChanged}" ref="infoWhiteHeader" :style="infoWhiteHeaderClip"><a href="javascript:void(0);" :style="{ cursor: triggerCursor }" @click="triggerClick" @mouseover="triggerOver" @mouseout="triggerOut">{{ itemTitle }}<br>{{ item.location }}</a></div>
              </h3>
              <div class="date">posted at {{ item.year }}.{{ item.month }}.{{ item.dayInMonth }}</div>
              <div class="desc" :style="styleInfoDesc">
                <div class="inr" :style="infoWhiteDescClip" :class="{black: infoWhiteDescChanged}" ref="infoWhiteDesc">{{ item.desc }}<template v-for="tool in item.tools"><br/>{{ tool }}</template></div>
              </div>
            </div>
        </div>
        <div class="mark mark-plus">+</div>
        <div class="mark mark-minus">_</div>
    </div>
    <!-- /info -->
  </div>
</template>
<script>
import CashDom from 'cash-dom'
import NaturalScroll from 'natural-scroll'

const MONTHS = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']

const PIXEL = 'px'
const DELAY_SHOW_TIME = 900

export default {
  name: 'work-item',
  props: ['item', 'index'],
  data () {
    return {
      workItemOv: false,
      itemClicked: false,
      isOpen: false,
      toggleOpen: false,
      teaserScrollArrowShow: false,
      teaserLoadingBgShow: false,
      teaserLoadingShow: false,
      teaserPicShow: true,
      teaserAbsClass: false,
      teaserHideClass: false,
      listShowClass: false,
      listWrapHeight: 0,
      infoWhiteHeaderChanged: false,
      infoWhiteDescChanged: false,
      infoWhiteHeaderClip: {
        clip: 'auto'
      },
      infoWhiteDescClip: {
        clip: 'auto'
      },
      defaultHeaderClip: '',
      defaultDescClip: '',
      styleInfoWhite: {
        marginLeft: '',
        marginRight: ''
      },
      styleInfoWhiteRev: {
        marginLeft: '',
        marginRight: ''
      },
      styleInfoHeader: {
        width: '',
        height: ''
      },
      styleInfoDesc: {
        width: '',
        height: ''
      },
      infoFixed: false,
      infoTop: 0,
      pictureHeight: 0,
      waitStartTime: 0
    }
  },
  computed: {
    triggerCursor () {
      return this.itemClicked ? 'wait' : 'pointer'
    },
    itemOdd () {
      return (this.index + 1) % 2 === 1
    },
    itemTitle () {
      return MONTHS[this.item.month - 1] + ' ' + this.item.year
    }
  },
  mounted () {
    this.calculateInfo()
    window.addEventListener('scroll', this.itemScroll)
  },
  destroy () {
    window.removeEventListener('scroll', this.itemScroll)
  },
  methods: {
    calculateInfo () {
      const header = this.$refs.infoHeader
      const desc = this.$refs.infoDesc
      const margin = 195
      let headerWidth = header.clientWidth
      let headerHeight = header.clientHeight
      let descWidth = desc.clientWidth
      let descHeight = desc.clientHeight
      this.styleInfoHeader.width = headerWidth + PIXEL
      this.styleInfoHeader.height = headerHeight + PIXEL
      this.styleInfoDesc.width = descWidth + PIXEL
      this.styleInfoDesc.height = descHeight + PIXEL
      if (this.itemOdd) {
        this.styleInfoWhite.marginLeft = margin + PIXEL
        this.styleInfoWhiteRev.marginLeft = -margin + PIXEL
      } else {
        this.styleInfoWhite.marginRight = margin + PIXEL
        this.styleInfoWhiteRev.marginRight = -margin + PIXEL
      }
      this.pictureHeight = this.$refs.originPicture.clientHeight
      this.defaultHeaderClip = this.getInfoClipParam(this.$refs.infoWhiteHeader)
      this.defaultDescClip = this.getInfoClipParam(this.$refs.infoWhiteDesc)
    },
    itemScroll () {
      const scrollTop = window.pageYOffset
      if (!this.isOpen) {
        return
      }

      const minY = this.getOffsetTop(this.$refs.workItem)
      const rangY = this.listWrapHeight - this.pictureHeight
      const maxY = minY + rangY
      let clipHeader = this.defaultHeaderClip
      let clipDesc = this.defaultDescClip
      const me = this
      console.log(`toggleOpen ${me.toggleOpen} minY ${minY} maxY ${maxY} rangeY ${rangY}`)
      if (this.toggleOpen && scrollTop > minY) {
        if (scrollTop < maxY) {
          this.infoFixed = true
          this.infoTop = 0
          const {dom: nearestHeaderPic, isBackgroundWhite: isHeaderPicWhite} = this.getNearestInfoPic(this.$refs.infoWhiteHeader)
          const {dom: nearestDescPic, isBackgroundWhite: isDescPicWhite} = this.getNearestInfoPic(this.$refs.infoWhiteDesc)
          clipHeader = this.getInfoClipParam(this.$refs.infoWhiteHeader, nearestHeaderPic)
          clipDesc = this.getInfoClipParam(this.$refs.infoWhiteDesc,
            nearestDescPic)
          this.toggleByBackground(true, isHeaderPicWhite)
          this.toggleByBackground(false, isDescPicWhite)
        } else {
          this.infoFixed = false
          this.infoTop = rangY
        }
      } else {
        this.infoFixed = false
        this.infoTop = 0
      }
      this.toggleInfoClip(true, clipHeader)
      this.toggleInfoClip(false, clipDesc)
    },
    triggerOver () {
      this.workItemOv = true
    },
    triggerOut () {
      this.workItemOv = false
    },
    triggerClick () {
      if (this.itemClicked) { return }
      this.itemClicked = true

      if (this.isOpen) {
        this.closeItem()
        return
      }

      this.waitOpenItem()
      const me = this
      setTimeout(() => {
        me.openItem()
      }, 200)
    },
    closeItem () {
      NaturalScroll.scrollTop(window.document.body, this.getOffsetTop(this.$refs.workItem) - 50, DELAY_SHOW_TIME)
      const me = this
      setTimeout(() => {
        me.listWrapHeight = 0
        me.listShowClass = false
        me.teaserPicShow = true
        me.teaserHideClass = false
        me.teaserAbsClass = false
        me.toggleOpen = false
        me.itemScroll()
        me.isOpen = false
        me.itemClicked = false
      }, DELAY_SHOW_TIME)
    },
    waitOpenItem () {
      this.waitStartTime = (new Date()).getTime()
      this.teaserLoadingBgShow = true
      this.teaserLoadingShow = true
      this.teaserPicShow = false
    },
    openItem () {
      const now = (new Date()).getTime()
      let delayTime = DELAY_SHOW_TIME - (now - this.waitStartTime)
      if (delayTime < 0) delayTime = 0

      this.teaserAbsClass = true
      this.teaserHideClass = false
      this.listShowClass = true
      this.listWrapHeight = this.pictureHeight
      this.itemScroll()

      const me = this
      // delay
      setTimeout(function () {
        me.teaserLoadingShow = false
        setTimeout(function () {
          me.teaserScrollArrowShow = true
          setTimeout(function () {
            me.teaserLoadingBgShow = false
            me.listWrapHeight = CashDom(me.$refs.picAreaList).outerHeight()
            me.itemClicked = false

            setTimeout(() => {
              if (me.isOpen) {
                me.teaserScrollArrowShow = false
                me.teaserHideClass = true
                me.teaserAbsClass = false
              }
            }, 2000)
          }, 200)
        }, 200)
      }, delayTime)

      // 初始化部分值
      this.isOpen = true
      this.toggleOpen = true
    },
    toggleInfoClip (headerOrDesc, clipObject) {
      const clipResult = `rect(${clipObject.t}px, ${clipObject.r}px, ${clipObject.b}px, ${clipObject.l}px)`
      if (headerOrDesc) {
        this.infoWhiteHeaderClip.clip = clipResult
      } else {
        this.infoWhiteDescClip.clip = clipResult
      }
    },
    toggleByBackground (headerOrDesc, isBackgroundWhite) {
      if (headerOrDesc) {
        this.infoWhiteHeaderChanged = isBackgroundWhite
      } else {
        this.infoWhiteDescChanged = isBackgroundWhite
      }
    },
    getNearestInfoPic (infoElem) {
      const infoBottom = this.getOffsetTop(infoElem) + infoElem.clientHeight
      const length = this.$refs.pictures.length
      for (let i = length - 1; i >= 0; i--) {
        const pictureDom = this.$refs.pictures[i]
        if (this.getOffsetTop(pictureDom) < infoBottom) {
          return { dom: pictureDom, isBackgroundWhite: this.item.content[i].isBackgroundWhite }
        }
      }
    },
    getInfoClipParam (infoElem, nearPicElem) {
      const width = infoElem.clientWidth
      const height = infoElem.clientHeight
      if (nearPicElem) {
        const infoTop = this.getOffsetTop(infoElem)
        const infoBottom = infoTop + height
        const picTop = this.getOffsetTop(nearPicElem)
        const picBottom = picTop + nearPicElem.clientHeight
        return {
          l: 0,
          r: width,
          t: picTop > infoTop ? picTop - infoTop : 0,
          b: picBottom < infoBottom ? picBottom - infoTop : height
        }
      } else {
        return {
          l: 0,
          r: width,
          t: 0,
          b: height
        }
      }
    },
    getOffsetTop (elem) {
      return CashDom(elem).offset().top
    }
  }
}
</script>
<style lang="scss">

.pic {
    position: relative;
    background: #f4f4f4;
    img {
        opacity: 0;
        filter: alpha(opacity=0);
        &.show {
            transition: all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
            opacity: 1;
            filter: alpha(opacity=100);
        }
    }
}

@keyframes scroll_down {
    0% {
        opacity: 0;
        transform: rotate(-45deg) translate3d(40px, -20px, 0);
    }
    30% {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        transform: rotate(-45deg) translate3d(0, 20px, 0);
    }
}

.work-item {
    position: relative;
    padding-bottom: 120px;
    &.ov .info .mark {
        opacity: 1;
        filter: alpha(opacity=100);
    }
    &.open .info .mark {
        &.mark-plus {
            visibility: hidden;
            opacity: 0;
            filter: alpha(opacity=0);
        }
        &.mark-minus {
            visibility: visible;
        }
    }
    &.rev {
        .info {
            position: absolute;
            left: auto;
            right: 0;
            text-align: right;
            &.fixed {
                right: 50%;
                margin-left: 0px;
                margin-right: -497.5px;
            }
            .mark {
                position: absolute;
                left: auto;
                right: -45px;
            }
            h3 {
                margin-left: 0px;
                float: right;
                .inr {
                    left: auto;
                    right: 0;
                }
            }
            .date {
                clear: both;
            }
            .desc {
                float: right;
                .inr {
                    left: auto;
                    right: 0;
                }
            }
        }
        .picArea {
            margin-right: 195px;
            margin-left: 0px;
        }
        .teaser {
            *zoom: 1;
            &:after {
                content: ".";
                display: block;
                clear: both;
                height: 0;
                visibility: hidden;
                overflow: hidden;
            }
            &.abs {
                left: auto;
                right: 0;
                top: 0;
            }
            .pic {
                float: right;
            }
        }
        .listWrap .list .row {
            *zoom: 1;
            &:after {
                content: ".";
                display: block;
                clear: both;
                height: 0;
                visibility: hidden;
                overflow: hidden;
            }
            .pic {
                float: right;
            }
        }
    }
}

.work-item .info {
    position: absolute;
    left: 0px;
    top: 0px;
    margin-top: 100px;
    .mark {
        position: absolute;
        left: -40px;
        top: 9px;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-style: normal;
        font-weight: 100;
        font-size: 65px;
        cursor: default;
        opacity: 0;
        filter: alpha(opacity=0);
        transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
        /* easeOutQuart */
        &.mark-plus {
            font-size: 55px;
        }
        &.mark-minus {
            margin-top: -29px;
            visibility: hidden;
        }
    }
    .infoClip {
        position: relative;
        overflow: hidden;
    }
    .infoInr {
        position: relative;
    }
    .black {
        color: #16161f !important;
        * {
            color: #16161f !important;
        }
    }
    &.info-white {
        height: 0;
        * {
            color: #fff;
        }
        .mark {
            display: none;
        }
        .date {
            visibility: hidden;
        }
    }
    &.fixed {
        position: fixed;
        left: 50%;
        margin-left: -497.5px;
    }
    h3 {
        position: relative;
        font-size: 48px;
        font-family: 'Cormorant Infant', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", serif;
        line-height: 48px;
        font-weight: 400;
        font-style: italic;
        margin-bottom: 10px;
        margin-top: 0;
        .inr {
            position: absolute;
            left: 0;
            top: 0;
            white-space: nowrap;
        }
        a {
            color: #16161f;
        }
    }
    .date {
        font-size: 10px;
        font-style: italic;
        // font-family: sans-serif;
        padding-bottom: 5px;
    }
    .desc {
        position: relative;
        font-size: 10px;
        font-style: italic;
        padding-bottom: 10px;
        .inr {
            position: absolute;
            left: 0;
            top: 0;
            white-space: nowrap;
        }
    }
}

.work-item .picArea {
    position: relative;
    margin-left: 195px;
}

.work-item .teaser {
    position: relative;
    &.abs {
        position: absolute;
        left: 0;
        top: 0;
    }
    &.hide {
        display: none;
        transition: all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
        /* easeInOutSine */
        opacity: 0;
        filter: alpha(opacity=0);
        visibility: hidden;
    }
    a .pic.hide {
        transition: all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
        /* easeInOutSine */
        opacity: 0;
        filter: alpha(opacity=0);
        visibility: hidden;
    }
    .black {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #16161f;
        opacity: 0;
        filter: alpha(opacity=0);
        visibility: hidden;
        transition: all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
        /* easeInOutSine */
        &.show {
            transition: all 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
            /* easeInOutSine */
            opacity: 1;
            filter: alpha(opacity=100);
            visibility: visible;
        }
    }
    .loadingLines {
        div {
            background: #fff;
        }
        &.hide {
            transition: all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
            /* easeInOutSine */
            opacity: 0;
            filter: alpha(opacity=0);
        }
    }
    .scrollArw {
        &.show {
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -30px;
            margin-top: -30px;
            div {
                width: 30px;
                height: 30px;
                border-left: 1px solid white;
                border-bottom: 1px solid white;
                animation: scroll_down .8s ease-in-out infinite;
            }
        }
        &.hide {
            transition: all 600ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
            /* easeInOutSine */
            opacity: 0;
            filter: alpha(opacity=0);
        }
    }
}

.work-item .listWrap {
    position: relative;
    height: 0;
    overflow: hidden;
    .list {
        position: relative;
        display: none;
        &.show {
          display: block;
        }
        .row {
          padding-bottom: 100px;
        }
    }
}
</style>



