<template>
    <div class="about-container">
      <div class="card about-card">
        <h1 class="title">{{ user.name }}</h1>
        <p class="desc">{{ user.profile }}</p>
        <img class="picture" :class="{ 'invisible': !user.picture }" :src="user.picture" />
        <p class="card-footer">Email: {{ user.email }}</p>
      </div>
    </div>    
</template>
<script>
export default {
  name: 'about',
  data () {
    return {
      loading: true,
      user: {}
    }
  },
  beforeMount () {
    this.$store.dispatch('FETCH_USER').then((user) => {
      this.user = user
      this.loading = false
    })
  }
}
</script>
<style lang="scss">

.about-card {
  margin: 0 auto;
  width: 540px;
  text-align: center;
  padding: 20px;
  .title {
    padding: 8px 0;
  }

  .desc {
    padding: 0 0 8px;
  }

  .picture {
    width: 480px;
    height: 360px;

    &.invisible {
      visibility: hidden;
    }
  }

}

</style>


