<template>
  <div class="work-container"  v-on:scroll="onScroll">
    <spinner :show="loading"></spinner>
    <transition name="slide-up">
      <div class="work-list">
        <transition-group tag="div" name="item">
          <item v-for="(item, index) in displayedItems" :key="item.id" :item="item" :index="index">
          </item>
        </transition-group>
      </div>
    </transition>
  </div>
</template>
<script>
import Spinner from './Spinner.vue'
import Item from './Item.vue'

const DELAY_SHOW_TIME = 900

export default {
  name: 'item-list',
  components: {
    Spinner,
    Item
  },
  props: {
    type: String
  },
  data () {
    return {
      loading: true,
      displayedItems: []
    }
  },
  beforeMount () {
    let startTime = (new Date()).getTime()
    const ACTION = this.type === 'design' ? 'FETCH_DESIGN' : 'FETCH_PHOTO'
    this.$store.dispatch(ACTION).then((items) => {
      this.displayedItems = items
      let delayTime = DELAY_SHOW_TIME - ((new Date()).getTime() - startTime)
      if (delayTime < 0) {
        delayTime = 0
      }
      const me = this
      setTimeout(() => {
        me.loading = false
      }, delayTime)
    })
  },
  methods: {
    onScroll (e, position) {
      console.log(e)
      console.log(position)
    }
  }
}
</script>
<style lang="scss">

.work-list {
  width: 995px;
  margin: 0 auto;
}
</style>



