<template>
  <div id="app">
    <div class="siteHeader">
      <h2><router-link to="/">copyright&copy; 2011-2017 MOMO SONG, all rights reserved.</router-link></h2>
      <div class="line"></div>
      <div class="rightBlock">
        <h1><router-link to="photography">Photography</router-link></h1>
        <div class="nav"><router-link to="design">design</router-link><span>·</span><router-link to="profile">profile</router-link></div>
      </div>
    </div>
    <transition name="fade" mode="out-in">
        <router-view class="view"></router-view>
    </transition>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'app',
  computed: {
    ...mapGetters([ 'isDesignActive', 'isPhotoActive', 'isUserActive' ])
  }
}
</script>

<style lang="scss">

@font-face {
  font-family: 'Cormorant Infant';
  font-style: normal;
  font-weight: 400;
  src: url(./assets/font/Cormorant-Infant.eot);
  src: local('Cormorant Infant'), local('CormorantInfant-Regular'), url(./assets/font/Cormorant-Infant.eot?#iefix) format('embedded-opentype'), url(./assets/font/Cormorant-Infant.svg#CormorantInfant) format('svg');
}

@font-face {
  font-family: 'Cormorant Infant';
  font-style: italic;
  font-weight: 300;
  src: local('Cormorant Infant Light Italic'), local('CormorantInfant-LightItalic'), url(./assets/font/Cormorant-Infant-Light-Italic.woff) format('woff'), url(./assets/font/Cormorant-Infant-Light-Italic.ttf) format('truetype'), url(./assets/font/Cormorant-Infant-Light-Italic.woff2) format('woff2');
}

@font-face {
  font-family: 'Cormorant Infant';
  font-style: italic;
  font-weight: 400;
  src: local('Cormorant Infant Italic'), local('CormorantInfant-Italic'), url(./assets/font/Cormorant-Infant-Italic.woff) format('woff'), url(./assets/font/Cormorant-Infant-Italic.ttf) format('truetype'), url(./assets/font/Cormorant-Infant-Italic.woff2) format('woff2');
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body {
  height: 100%;
  background: #fff;
}

html {
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  height: 100%;
  overflow-y: scroll;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  /* Fix for webkit rendering */
  -webkit-text-size-adjust: 100%;
  font-style: italic;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
  margin: 0;
}

a {
    text-decoration: none;
    color: #656575;
    &:hover {
      color: #16161f;
    }
}

.card {
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.2);
}

.align-center {
  text-align: center
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.siteHeader {
  width: 995px;
  margin: 0 auto;
  *zoom: 1;
  padding: 100px 0;
  &:after {
    content: ".";
    display: block;
    clear: both;
    height: 0;
    visibility: hidden;
    overflow: hidden;
  }
  h2 {
    font-family: 'Cormorant Infant', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", serif;
    font-size: 21px;
    float: left;
    height: 100px;
    line-height: 100px;
    padding-left: 147.5px;
    a {
      color: #16161f;
    }
  }
  .line {
    background: url(./assets/line.png) no-repeat;
    background-size: 100px 100px;
    float: left;
    width: 100px;
    height: 100px;
    line-height: 100px;
  }
  .rightBlock {
    float: left;
    padding-top: 25px;
    font-family: 'Cormorant Infant', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", serif;
    h1 {
      text-align: center;
      a {
        color: #16161f;
      }
    }
    .nav {
      a.noLink {
        text-decoration: line-through;
      }
      span {
        padding: 0 4px;
      }
    }
  }
}

@keyframes piston {
  0% {
    -webkit-transform: rotate(22deg) translate3d(0, -25px, 0);
    -moz-transform: rotate(22deg) translate3d(0, -25px, 0);
    -ms-transform: rotate(22deg) translate3d(0, -25px, 0);
    -o-transform: rotate(22deg) translate3d(0, -25px, 0);
    transform: rotate(22deg) translate3d(0, -25px, 0);
  }
  50% {
    -webkit-transform: rotate(22deg) translate3d(0, 25px, 0);
    -moz-transform: rotate(22deg) translate3d(0, 25px, 0);
    -ms-transform: rotate(22deg) translate3d(0, 25px, 0);
    -o-transform: rotate(22deg) translate3d(0, 25px, 0);
    transform: rotate(22deg) translate3d(0, 25px, 0);
  }
  100% {
    -webkit-transform: rotate(22deg) translate3d(0, -25px, 0);
    -moz-transform: rotate(22deg) translate3d(0, -25px, 0);
    -ms-transform: rotate(22deg) translate3d(0, -25px, 0);
    -o-transform: rotate(22deg) translate3d(0, -25px, 0);
    transform: rotate(22deg) translate3d(0, -25px, 0);
  }
}

@keyframes piston_rev {
  0% {
    -webkit-transform: rotate(22deg) translate3d(0, 25px, 0);
    -moz-transform: rotate(22deg) translate3d(0, 25px, 0);
    -ms-transform: rotate(22deg) translate3d(0, 25px, 0);
    -o-transform: rotate(22deg) translate3d(0, 25px, 0);
    transform: rotate(22deg) translate3d(0, 25px, 0);
  }
  50% {
    -webkit-transform: rotate(22deg) translate3d(0, -25px, 0);
    -moz-transform: rotate(22deg) translate3d(0, -25px, 0);
    -ms-transform: rotate(22deg) translate3d(0, -25px, 0);
    -o-transform: rotate(22deg) translate3d(0, -25px, 0);
    transform: rotate(22deg) translate3d(0, -25px, 0);
  }
  100% {
    -webkit-transform: rotate(22deg) translate3d(0, 25px, 0);
    -moz-transform: rotate(22deg) translate3d(0, 25px, 0);
    -ms-transform: rotate(22deg) translate3d(0, 25px, 0);
    -o-transform: rotate(22deg) translate3d(0, 25px, 0);
    transform: rotate(22deg) translate3d(0, 25px, 0);
  }
}

.loadingLines {
  position: absolute;
  left: 50%;
  top: 50%;
}

.loadingLines.show div {
  position: absolute;
  left: 0;
  top: -30px;
  width: 1px;
  height: 60px;
  background: #16161f;
}

.loadingLines.show .line1 {
  margin-top: -6px;
  -webkit-animation: piston .9s ease-in-out infinite;
  -moz-animation: piston .9s ease-in-out infinite;
  -ms-animation: piston .9s ease-in-out infinite;
  -o-animation: piston .9s ease-in-out infinite;
  animation: piston .9s ease-in-out infinite;
}

.loadingLines.show .line2 {
  margin-top: 6px;
  -webkit-animation: piston_rev .9s ease-in-out infinite;
  -moz-animation: piston_rev .9s ease-in-out infinite;
  -ms-animation: piston_rev .9s ease-in-out infinite;
  -o-animation: piston_rev .9s ease-in-out infinite;
  animation: piston_rev .9s ease-in-out infinite;
}

</style>



