import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const API_DESIGN = 'static/design.json'
const API_PHOTOGRAPHY = 'static/photography.json'
const API_USER = 'static/user.json'

const store = new Vuex.Store({
  state: {
    user: {},
    design: [],
    photography: []
  },
  actions: {
    FETCH_DESIGN: ({ commit, state }) => {
      return Vue.http.get(API_DESIGN).then((response) => {
        let data = response.body
        state.design.push(...data.list)
        return state.design
      })
    },
    FETCH_PHOTO: ({ commit, state }) => {
      return Vue.http.get(API_PHOTOGRAPHY).then((response) => {
        let data = response.body
        state.photography.push(...data.list)
        return state.photography
      })
    },
    FETCH_USER: ({ commit, state }) => {
      return Vue.http.get(API_USER).then((response) => {
        let data = response.body
        state.user = data
        return state.user
      })
    }
  },
  mutations: {},
  getters: {
    isDesignActive (state) {
      return state.route.path === '/design'
    },
    isPhotoActive (state) {
      return state.route.path === '/photography'
    },
    isUserActive (state) {
      return state.route.path === '/about'
    }
  }
})

export default store



